import React,{Component} from 'react'
import {connect} from 'react-redux'
import TextField from 'material-ui/TextField'
import RaisedButton from 'material-ui/RaisedButton'
import DropDownMenu from 'material-ui/DropDownMenu'
import MenuItem from 'material-ui/MenuItem'
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';
import {Toolbar, ToolbarGroup, ToolbarSeparator, ToolbarTitle} from 'material-ui/Toolbar';
import classes from './style.scss'
import {getUsers} from '../../../store/actions/apanel'

class Users extends Component {
  constructor() {
    super()
    this.state = {
      sortBy: 1
    }
  }
  componentWillMount() {
    this.props.getUsers()
  }
  handleSortChange = (event, index, value) => this.setState(Object.assign(this.state, {sortBy: value}))
  render() {

    return (
      <div>
        <Toolbar>
          <ToolbarGroup>
            <ToolbarTitle text="Actions" />
          </ToolbarGroup>
          <ToolbarGroup>
            <ToolbarSeparator />
            <RaisedButton label="New" primary={true} />
          </ToolbarGroup>
        </Toolbar>
        <TextField
          style={{width:'100%'}}
          floatingLabelText="Search"
        />


      </div>
    )
  }
}
const mapStateToProps = state => state.apanel.page

const mapDispatchToProps = dispatch => ({
  getUsers: () => dispatch(getUsers())
})

export default connect(mapStateToProps, mapDispatchToProps)(Users)



